import React, { useState, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { AuthContext } from '../services/AuthContext'; // Importa el contexto de autenticacin para el login
import Header from '../components/Header';  // Importa el Header (barra de navegacin)
import Footer from '../components/Footer';  // Importa el Footer (pie de pgina)

function AdminLogin() {
    // Hooks de estado para gestionar los valores del formulario y los errores
    const [email, setEmail] = useState('');  // Email ingresado por el usuario
    const [password, setPassword] = useState('');  // Contrasea ingresada por el usuario
    const [errors, setErrors] = useState({});  // Almacena los errores de validacin del formulario

    // Extrae la funcin 'login' del contexto de autenticacin
    const { login } = useContext(AuthContext);

    // Hook para la navegacin programtica (redirigir a otras rutas)
    const navigate = useNavigate();

    // Funcin para validar los campos del formulario
    const validateForm = () => {
        const newErrors = {};  // Objeto para almacenar los errores de validacin

        // Validacin del email
        if (!email) {
            newErrors.email = 'El correo electrnico es requerido'; // Error si no se ingresa un email
        } else if (!/@/.test(email)) {
            newErrors.email = 'El correo electrnico debe contener un @'; // Error si no contiene '@'
        } else if (!/\S+@\S+\.\S+/.test(email)) {
            newErrors.email = 'El correo electrnico no es vlido. Debe seguir el formato ejemplo@dominio.com'; // Error si el formato es incorrecto
        }

        // Validacin de la contrasea
        if (!password) {
            newErrors.password = 'La contrasea es requerida';  // Error si no se ingresa una contrasea
        }

        setErrors(newErrors);  // Actualiza el estado de los errores
        return Object.keys(newErrors).length === 0;  // Devuelve true si no hay errores
    };

    // Funcin para manejar el inicio de sesin del administrador
    const handleAdminLogin = async () => {
        // Valida el formulario antes de hacer el login
        if (validateForm()) {
            try {
                // Llama a la funcin login desde el contexto, pasando el email, contrasea y un parmetro 'true' para indicar que es un login de administrador
                const response = await login(email, password, true);
                if (response && response.status === 'success') {
                    // Si el login es exitoso, redirige al usuario a la pgina principal
                    navigate('/');
                } else {
                    alert('Error en el inicio de sesin');  // Si hay un error en el login, muestra un mensaje
                }
            } catch (error) {
                console.error('Error al iniciar sesin:', error);
                alert('Error en el inicio de sesin');  // Manejo de error en caso de fallo en la llamada a login
            }
        } else {
            alert('Por favor, corrige los errores en el formulario');  // Muestra un mensaje si hay errores en el formulario
        }
    };

    return (
        <div className="d-flex flex-column min-vh-100" style={{ background: '#f0f0f0'}}>
            {/* Componente Header que contiene la barra de navegacin */}
            <Header />
            <div className="container my-5">
                <div className="row justify-content-center">
                    <div className="col-md-6 col-lg-4">
                        {/* Card que contiene el formulario de login */}
                        <div className="card shadow-lg p-4">
                            <h2 className="text-center mb-4">Iniciar sesin como Administrador</h2>
                            <div className="mb-3">
                                {/* Campo de email */}
                                <label htmlFor="email" className="form-label">Correo electrnico</label>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="email"
                                    placeholder="Introduce tu correo"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}  // Actualiza el estado 'email' cuando el usuario escribe
                                />
                                {errors.email && <div className="text-danger">{errors.email}</div>}  {/* Muestra el error de email si existe */}
                            </div>
                            <div className="mb-3">
                                {/* Campo de contrasea */}
                                <label htmlFor="password" className="form-label">Contrasea</label>
                                <input
                                    type="password"
                                    className="form-control"
                                    id="password"
                                    placeholder="Introduce tu contrasea"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}  // Actualiza el estado 'password' cuando el usuario escribe
                                />
                                {errors.password && <div className="text-danger">{errors.password}</div>}  {/* Muestra el error de contrasea si existe */}
                            </div>
                            {/* Botn para enviar el formulario */}
                            <button
                                className="btn btn-primary w-100"
                                onClick={handleAdminLogin}  // Llama a la funcin 'handleAdminLogin' al hacer clic
                            >
                                Ingresar
                            </button>
                            {/* Enlace para redirigir a la pgina de login de usuario */}
                            <div className="text-center mt-3">
                                <Link to="/login">Iniciar sesin como Usuario cliente</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* Componente Footer que contiene el pie de pgina */}
            <Footer />
        </div>
    );
}

export default AdminLogin;
