import React, { useState, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom'; // Importar Link
import { AuthContext } from '../services/AuthContext';
import Header from '../components/Header';
import Footer from '../components/Footer';

function Login() {
    // Estados locales para manejar los datos del formulario y los errores
    const [email, setEmail] = useState('');  // Estado para el correo electrnico
    const [password, setPassword] = useState('');  // Estado para la contrasea
    const [errors, setErrors] = useState({});  // Estado para almacenar los errores de validacin

    const { login } = useContext(AuthContext);  // Obtener la funcin de login desde el contexto
    const navigate = useNavigate();  // Hook para navegar entre pginas

    // Funcin para validar los campos del formulario
    const validateForm = () => {
        const newErrors = {};  // Objeto para almacenar los errores de validacin

        // Validacin del correo electrnico
        if (!email) {
            newErrors.email = 'El correo electrnico es requerido';
        } else if (!/@/.test(email)) {
            newErrors.email = 'El correo electrnico debe contener un @';
        } else if (!/\S+@\S+\.\S+/.test(email)) {
            newErrors.email = 'El correo electrnico no es vlido. Debe seguir el formato ejemplo@dominio.com';
        }

        // Validacin de la contrasea
        if (!password) {
            newErrors.password = 'La contrasea es requerida';
        }

        setErrors(newErrors);  // Actualiza el estado de los errores
        return Object.keys(newErrors).length === 0;  // Retorna true si no hay errores
    };

    // Funcin para manejar el inicio de sesin
    const handleLogin = async () => {
        if (validateForm()) {  // Solo si el formulario es vlido
            await login(email, password);  // Llama a la funcin de login del contexto
            navigate('/');  // Redirige a la pgina principal tras iniciar sesin
        } else {
            alert('Por favor, corrige los errores en el formulario');  // Muestra un mensaje si el formulario tiene errores
        }
    };

    return (
        <div className="d-flex flex-column min-vh-100" style={{ background: '#f0f0f0'}}>
            <Header />  
            <div className="container my-5">
                <div className="row justify-content-center">
                    <div className="col-md-6 col-lg-4">
                        <div className="card shadow-lg p-4">
                            <h2 className="text-center mb-4">Iniciar sesin</h2>
                            <div className="mb-3">
                                <label htmlFor="email" className="form-label">Correo electrnico</label>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="email"
                                    placeholder="Introduce tu correo"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}  // Actualiza el estado del email
                                />
                                {/* Muestra un mensaje de error si el email es invlido */}
                                {errors.email && <div className="text-danger">{errors.email}</div>}
                            </div>
                            <div className="mb-3">
                                <label htmlFor="password" className="form-label">Contrasea</label>
                                <input
                                    type="password"
                                    className="form-control"
                                    id="password"
                                    placeholder="Introduce tu contrasea"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}  // Actualiza el estado de la contrasea
                                />
                                {/* Muestra un mensaje de error si la contrasea est vaca */}
                                {errors.password && <div className="text-danger">{errors.password}</div>}
                            </div>
                            {/* Botn para enviar el formulario e iniciar sesin */}
                            <button
                                className="btn btn-primary w-100"
                                onClick={handleLogin}
                            >
                                Ingresar
                            </button>
                            <div className="text-center mt-3">
                                {/* Enlace para iniciar sesin como administrador */}
                                <Link to="/admin-login">Iniciar sesin como administrador</Link>
                            </div>
                            <div className="text-center mt-3">
                                {/* Enlace para redirigir a la pgina de registro */}
                                <p>No tienes cuenta? <Link to="/register">Regstrate</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />  
        </div>
    );
}

export default Login;
